import '../styles/AdminMenu.css';
import { Link } from 'react-router-dom'

const AdminMenu = () => {
  return (
    <>
      <div className='admin_page_left'>
        <h1> Admin Pannel </h1>
        <div className='admin_page_left_btn'>
          <Link to="/profile/admin/create-category">
            <button className='btns'>
              Create Category
            </button>
          </Link>
          <Link to="/profile/admin/create-product">
            <button className='btns'>
              Create Products
            </button>
          </Link>
          <Link to="/profile/admin/products">
            <button className='btns'>
              All Products
            </button>
          </Link>
          <Link to="/profile/admin/admin-order">
            <button className='btns'>
              Orders
            </button>
          </Link>
        </div>
      </div>
    </>
  )
}

export default AdminMenu
