import '../../styles/AdminDash.css';
import '../../styles/Products.css';
import React, { useEffect, useState } from 'react'
import AdminMenu from '../../components/AdminMenu'
import toast from 'react-hot-toast';
import axios from 'axios';
import Currency from '../../components/Currency';
import { IoCart } from "react-icons/io5";
import { Link } from 'react-router-dom';

const Products = () => {
  const [products, setProducts] = useState([])

  // get all products
  const getAllProducts = async () => {
    try {
      const { data } = await axios.get(`/api/v1/product/get-product`)
      setProducts(data.products);

    } catch (error) {
      toast.error("Something went wrong")
    }
  }

  useEffect(() => {
    getAllProducts();
  },[])

  return (
    <>
      <div className="admin_page">
      <div className="admin_menu">
        <AdminMenu />
      </div>
      <div className="admin_right">
        <div className="admin_right_prod">
          <h2> All Products </h2>
          <h3> {products.length} Products </h3>
          <div className='admin_products'>
            {products?.map(p => {
              return (
                <>
                <Link key={p._id} to={`/profile/admin/products/${p.slug}`} className='card-link'>
                  <div className="card">
                    <div className="card-img">
                      <img src={`/api/v1/product/product-photo/${p._id}`} alt={p.name} />
                    </div>
                    <div className="card-info">
                      <p className="text-title"> {p.name} </p>
                      <p className="text-body"> {p.description} </p>
                    </div>
                    <div className="card-footer">
                      <span className="text-title"> {<Currency price={p.price} />} </span>
                      <Link className='card-button-link'>
                        <div className="card-button">
                          <IoCart className='cart-icon'/>
                        </div>
                      </Link>
                    </div>
                  </div>
                </Link>
                </>
              )
            })}
          </div>
        </div>
      </div>
    </div>
    </>
  )
}

export default Products
